import React from "react";

function HomeEducation() {
    return (
        <div className="home-education">
            <h2>Education</h2>

            <div className="row">
                <div className="col-lg-3 education-left position-relative">
                    <img className="education-icon" src="images/university.shield.small.white.png" alt="Johns Hopkins University Logo"/>
                </div>

                <div className="col-lg-9">
                    <h2>Johns Hopkins University</h2>
                    <ul>
                        <li>MSE Robotics (GPA: 4.0/4.0) <span className="left-align">Aug 2020 ~ May 2021</span></li>
                        <li>BS Computer Science (GPA: 4.0/4.0) <span className="left-align">Aug 2016 ~ May 2020</span></li>
                        <li>BS Biomedical Engineer (GPA: 4.0/4.0) <span className="left-align">Aug 2016 ~ May 2020</span></li>
                        <br/>
                        <li>Dean's List (GPA {">"} 3.5) <span className="left-align">All semesters</span></li>
                        <li>Richard J. Johns Award <span className="left-align">2020</span></li>
                        <li>Outstanding Senior Award <span className="left-align">2020</span></li>
                        <li>Most Valuable Peer Award <span className="left-align">2019</span></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default HomeEducation;

