import React from "react";
import { Link } from "react-router-dom";

function NavBarText(props) {
  const listItemClasses = "nav-item";
  const linkItemClasses = "nav-link navbar-text-style";

  return (
    <li className={listItemClasses}>
      <Link className={linkItemClasses} to={props.navLink}>
        {props.navText}
      </Link>
    </li>
  );
}

function NavBarIcon(props) {
    const listItemClasses = "nav-item";
    const linkItemClasses = "nav-link navbar-icon-style";
  
    return (
      <li className={listItemClasses}>
        <a className={linkItemClasses} href={props.navLink}>
            <i className={props.icon}></i>
        </a>
      </li>
    );
}

function NavBar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark navbar-main fixed-top">
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav">
          <NavBarText navLink="/" navText="About" />
          <NavBarText navLink="/projects" navText="Projects" />
          <NavBarText navLink="/wip" navText="WIP" />
        </ul>
        <ul className="navbar-nav ml-auto">
            <NavBarIcon navLink="https://github.com/alanx3x7" icon="fab fa-github"/>
            <NavBarIcon navLink="https://www.linkedin.com/in/alan-lai-0129" icon="fab fa-linkedin"/>
        </ul>
      </div>
    </nav>
  );
}

export default NavBar;
